
import { defineComponent, onMounted, ref } from "vue";
import DataCard from "@/components/resource/DataCard.vue";
import {
  fuzzyQueryDataList,
  getHot,
  getIdAndDataListName,
} from "@/api/request";
import router from "@/router";
import NProgress from "nprogress";
import { Search } from "@element-plus/icons-vue";
import PageCopyright from "@/layout/PageCopyright.vue";
import { DataListType } from "@/type";
import { classList } from "@/common-config";
// NProgress.configure({ showSpinner: false });

export default defineComponent({
  components: {
    DataCard,
    PageCopyright,
  },

  setup() {
    const hotDataList = ref<{ dataListName: string; id: string }[]>([]);
    const specialList = ref<{ dataListName: string; id: string }[]>([]);
    const sortWord = ref("update_time");
    const skeletonFlag = ref(true);
    const hotSkeletonFlag = ref(true);
    const input = ref("");
    const classValue = ref("所有");

    const titleKeyword = ref("");

    const fileList = ref<DataListType[]>([]);
    const total = ref(0);
    const currentPage = ref(1);

    const specialLoading = ref(false);

    const searchData = async (
      page: number,
      size: number,
      titleKeyword: string,
      property: string,
      type: string
    ) => {
      const jsonData = {
        page: page,
        size: size,
        titleKeyword: titleKeyword,
        property: property,
        flag: false,
        type: type,
      };
      NProgress.start();
      const data = await fuzzyQueryDataList(jsonData);
      if (data != null && (data as any).code === 0) {
        fileList.value = data.data.list;
        total.value = data.data.total;
      }
      NProgress.done();
    };

    const searchHandle = async () => {
      titleKeyword.value = input.value;
      await searchData(
        0,
        8,
        titleKeyword.value,
        sortWord.value,
        classValue.value === "所有" ? "" : classValue.value
      );
      currentPage.value = 1;
    };

    const classifyHandle = async (val: string) => {
      classValue.value = val;
      await searchData(
        0,
        8,
        "",
        sortWord.value,
        classValue.value === "所有" ? "" : classValue.value
      );
      currentPage.value = 1;
      input.value = "";
      titleKeyword.value = "";
    };

    const sortHandle = async (val: string) => {
      sortWord.value = val;
      await searchData(
        currentPage.value - 1,
        8,
        titleKeyword.value,
        sortWord.value,
        classValue.value === "所有" ? "" : classValue.value
      );
    };

    const pageChange = async (val: number) => {
      await searchData(
        val - 1,
        8,
        titleKeyword.value,
        sortWord.value,
        classValue.value === "所有" ? "" : classValue.value
      );
      input.value = titleKeyword.value;
    };

    const toDetail = (index: number) => {
      router.push({
        name: "resourceDetail",
        params: {
          id: fileList.value[index].id,
          // fileInfo: JSON.stringify(fileList.value[index]),
        },
      });
    };
    const toHotData = (val: number) => {
      router.push({
        name: "resourceDetail",
        params: {
          id: hotDataList.value[val].id,
        },
      });
    };

    const toSpecial = (val: string) => {
      router.push({
        name: "resourceDetail",
        params: {
          id: val,
        },
      });
    };

    const changeSpecialData = async () => {
      specialLoading.value = true;
      const specialData = await getIdAndDataListName(4);
      specialLoading.value = false;
      if (specialData != null && (specialData as any).code === 0) {
        specialList.value = [];
        specialData.data.forEach((item: { name: string; id: string }) => {
          specialList.value.push({
            dataListName: item.name,
            id: item.id,
          });
        });
      }
    };

    onMounted(async () => {
      await searchData(0, 8, "", "update_time", "");
      const data = await getHot(8);
      if (data != null && (data as any).code === 0) {
        hotDataList.value = data.data;
      }
      const specialData = await getIdAndDataListName(4);
      if (specialData != null && (specialData as any).code === 0) {
        specialList.value = [];
        specialData.data.forEach((item: { name: string; id: string }) => {
          specialList.value.push({
            dataListName: item.name,
            id: item.id,
          });
        });
      }
      skeletonFlag.value = false;
      hotSkeletonFlag.value = false;
    });

    return {
      skeletonFlag,
      hotSkeletonFlag,
      input,
      fileList,
      total,
      toDetail,
      currentPage,
      pageChange,
      sortWord,
      classList,
      hotDataList,
      specialList,
      toSpecial,
      Search,
      searchHandle,
      classValue,
      classifyHandle,
      sortHandle,
      toHotData,
      titleKeyword,
      changeSpecialData,
      specialLoading,
    };
  },
});
